import React from "react";
import "../App.css";
import { Button } from "./Button";
import "./herosection.css";
import TextTransition, { presets } from "react-text-transition";
import Keyframe from "../Assets/keyframe.png";
import Video from "../Assets/SR.mp4";
import ReactPlayer from "react-player";


function HeroSection() {
  const TEXTS = ["Anmol", "Lomna"];
  const [index, setIndex] = React.useState(0);

  React.useEffect(() => {
    const intervalId = setInterval(
      () => setIndex((index) => index + 1),
      2500 // every 3 seconds
    );
    return () => clearTimeout(intervalId);
  }, []);

  const src = "https://www.youtube.com/embed/pQEMi5MFfpE";

  return (
    <>
      <div className="hero-section">
        <div className="hero-container">
          <h1>
            {" "}
            Hi, I'm&nbsp;
            <TextTransition
              inline={true}
              text={TEXTS[index % TEXTS.length]}
              springConfig={presets.stiff}
            />
          </h1>
          <div className="hero_subtitle">
            <p>I'm a pixel pusher</p>
            <img className="pixel" src={Keyframe}></img>
          </div>
          <div className="hero-btns">
            <Button url={"mailto:anmol_k@cy.iitr.ac.in"}>Send a Message</Button>
          </div>
        </div>
        <ReactPlayer className="showreel"
       url={"https://www.youtube.com/embed/pQEMi5MFfpE?controls=0"}
        playing={true}
        muted={true}
        width={"100%"}
        height={"132%"}
        loop={"true"}
/>
      </div>
    </>
  );
}

export default HeroSection;
